<template>
  <div>
    <!--头部-->
    <!-- <mt-header fixed title="首页"></mt-header> -->
    <!---内容区域-->
    <div class="content">
      <mt-navbar v-model="selected" class="fixedNav">
        <mt-tab-item id="1"><div class="tabBorder" @click="haveVisType1">访客预约</div></mt-tab-item>
        <mt-tab-item id="2"><div class="tabBorder" @click="haveVisType2">个人预约</div></mt-tab-item>
      </mt-navbar>

      <!-- tab-container -->
      <mt-tab-container v-model="selected" class="marTopNav">
          <mt-tab-container-item id="1">
            <div class="main-body" ref="wrapper" :style="{ width: '100%',height: (wrapperHeight-20) + 'px' }">
                <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" @bottom-status-change="handleBottomChange" :bottom-all-loaded="allLoaded" ref="loadmore" :autoFill="isAutoFill">
                  <div class="rongUl">
                    <p v-if="len1" style="text-align:center;margin-top:50px;">暂无数据</p>
                    <ul class="mui-table-view mui-grid-view plateList">
                      <li v-for="(item,index) in dataCompany" :key="index" class="mui-table-view-cell mui-media mui-col-xs-6 plateLi flexBetween" @click="toCompanyDetails(item.recordInnerId,dataCompany[index])">
                          <!-- <img class="mui-media-object" v-lazy="item.img"> -->
                          <div>
                            <!-- <div>
                              <span class="plateNumber" v-text="item.visitorCarCode"></span>
                              <span v-if="item.tips=='逾期失效'" class="recordTips" v-text="item.tips"></span>
                              <span v-else class="recordTips recordTipsOver" v-text="item.tips"></span>
                            </div> -->
                            <div class="listDiv">
                              <span class="plateVisName ellipsis" v-text="item.visitorName"></span>
                              <span class="plateNumber" v-text="item.visitorCarCode"></span>
                              <span v-if="item.visitorState==1" class="plateTips">待入场</span>
                              <span v-else-if="item.visitorState==3" class="plateTips plateTipsOverHui">已取消</span>
                              <span v-else-if="item.visitorState==4" class="plateTips plateTipsOver">已入场</span>
                              <span v-else-if="item.visitorState==5" class="plateTips plateTipsOver">已支付</span>
                              <span v-else-if="item.visitorState==6" class="plateTips plateTipsOverHui2">已完成</span>
                              <span v-else-if="item.visitorState==7" class="plateTips plateTipsOverHui">已出场</span>
                              <span v-else class="plateTips plateTipsOverRed">已逾期</span>
                            </div>
                            <div class="plateTime">
                                <span class="plateStartTime" v-text="cutTime(item.visitorBeginTime)"></span> —
                                <span class="plateEndTime" v-text="cutTime(item.visitorEndTime)"></span>
                            </div>
                          </div>
                          <div>
                            <div class="money">￥<span v-if="item.money==null" v-text="0"></span><span v-else v-text="item.money/100"></span></div>
                            <div class="jian"></div>
                          </div>
                      </li>
                    </ul>
                    <div class="noMoreData" v-if="more">没有更多数据了...</div>
                  </div>
                </mt-loadmore>
                
            </div>
          </mt-tab-container-item>
          <mt-tab-container-item id="2">
            <div class="main-body" ref="wrapper">
                <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" @bottom-status-change="handleBottomChange" :bottom-all-loaded="allLoaded" ref="loadmore1" :autoFill="isAutoFill">
                  <div class="rongUl">
                    <p v-if="len2" style="text-align:center;margin-top:50px;">暂无数据</p>
                    <ul class="mui-table-view mui-grid-view plateList">
                      <li v-for="(item,index) in datas" :key="index" class="mui-table-view-cell mui-media mui-col-xs-6 plateLi flexBetween" @click="toDetails(item.recordInnerId,datas[index])">
                          <!-- <img class="mui-media-object" v-lazy="item.img"> -->
                          <div>
                            <div class="listDiv">
                              <span class="plateNumber" v-text="item.visitorCarCode"></span>
                              <span v-if="item.visitorState==1" class="plateTips">待入场</span>
                              <span v-else-if="item.visitorState==3" class="plateTips plateTipsOverHui">已取消</span>
                              <span v-else-if="item.visitorState==4" class="plateTips plateTipsOver">已入场</span>
                              <span v-else-if="item.visitorState==5" class="plateTips plateTipsOver">已支付</span>
                              <span v-else-if="item.visitorState==6" class="plateTips plateTipsOverHui2">已完成</span>
                              <span v-else-if="item.visitorState==7" class="plateTips plateTipsOverHui">已出场</span>
                              <span v-else class="plateTips plateTipsOverRed">已逾期</span>
                            </div>
                            <div class="plateTime">
                                <span class="plateStartTime" v-text="cutTime(item.visitorBeginTime)"></span> —
                                <span class="plateEndTime" v-text="cutTime(item.visitorEndTime)"></span>
                            </div>
                          </div>
                          <div>
                            <div class="money">￥<span v-if="item.money==null" v-text="0"></span><span v-else v-text="item.money/100"></span></div>
                            <div class="jian"></div>
                          </div>
                      </li>
                    </ul>
                    <div class="noMoreData" v-if="more">没有更多数据了...</div>
                  </div>
                </mt-loadmore>
            </div>
          </mt-tab-container-item>
      </mt-tab-container>
    </div>

  </div>
</template>
<script>
  import { Toast } from 'mint-ui'
  export default {
    name: 'record',
    data: function(){
      return {
        len1: false,
        len2: false,
        more: false,
        dataCompany: [],
        visitorName: '',
        visitorCarCode: '',
        datas: [
        //   {
        //   visitorCarCode: '京Q87W78',
        //   visitorState: 2,
        //   visitorBeginTime: '07/12 17:00:22',
        //   visitorEndTime: '07/12 19:00:33',
        //   money: '4.00'
        // }
        // ,{
        //   index: 1,
        //   title: '京A88888',
        //   tips: '已取消',
        //   startTime: '07/29 09:00',
        //   endTime: '07/29 18:00',
        //   money: '104.00'
        // },{
        //   index: 2,
        //   title: '京Q87W78',
        //   tips: '逾期失效',
        //   startTime: '07/12 17:00',
        //   endTime: '07/12 19:00',
        //   money: '4.00'
        // },{
        //   index: 3,
        //   title: '京Q87W78',
        //   tips: '已取消',
        //   startTime: '07/12 17:00',
        //   endTime: '07/12 19:00',
        //   money: '4.00'
        // },{
        //   index: 4,
        //   title: '京Q87W78',
        //   tips: '已取消',
        //   startTime: '07/12 17:00',
        //   endTime: '07/12 19:00',
        //   money: '4.00'
        // },{
        //   index: 5,
        //   title: '京Q87W78',
        //   tips: '已取消',
        //   startTime: '07/12 17:00',
        //   endTime: '07/12 19:00',
        //   money: '0.00'
        // },{
        //   index: 6,
        //   title: '京Q87W78',
        //   tips: '已取消',
        //   startTime: '07/12 17:00',
        //   endTime: '07/12 19:00',
        //   money: '4.00'
        // },{
        //   index: 7,
        //   title: '京A88888',
        //   tips: '已取消',
        //   startTime: '07/29 09:00',
        //   endTime: '07/29 18:00',
        //   money: '4.00'
        // },{
        //   index: 8,
        //   title: '京A88888',
        //   tips: '已取消',
        //   startTime: '07/29 09:00',
        //   endTime: '07/29 18:00',
        //   money: '4.00'
        // }
        ],
        //可以进行上拉
        allLoaded: false,
        //是否自动触发上拉函数
        isAutoFill: false,
        wrapperHeight: 0,
        courrentPage: 0,
        page: 1,
        selected: '1',
        visType: 1
      }
    },
    mounted() {
      // 父控件要加上高度，否则会出现上拉不动的情况
      this.wrapperHeight =
        document.documentElement.clientHeight -
        this.$refs.wrapper.getBoundingClientRect().top;
    },
    created(){
      if(this.$route.query.iswhere=="record"){
        this.$route.meta.title='历史记录'
        window.document.title = '历史记录'
      }
      this.getParams()
      this.getRecord()
    },
    methods: {
      cutTime(time){
        if (time !== '' && time !== null) {
            time = time.substring(0,16);
            return time;
          }
      },
      // 取到路由带过来的参数
      getParams() {
        var that = this;
        that.selected = that.$route.query.selected
        if( that.selected == '1' ){
          that.visType = 1
        }else{
          that.visType = 2
        }
      },
      getRecord(){
        var that = this
        that.$axios.post("getCompanyVistor",{
          page: that.page,
          size: 10,
          OrderType: that.visType,
          VisitorStateHis: '1,2,3,4,5,6,7,8',
          requestType: 1
        })
        .then(response => {
          if( response.data.status == 200 ){
            if( that.visType == 1 ){
                that.dataCompany = response.data.result;
                var length1 = that.dataCompany.length
                // alert(length1)
                if( length1 < 1){
                  that.len1 = true
                }else{
                  that.len1 = false
                }
            }else{
              that.datas = response.data.result;
              var length2 = that.datas.length
              // alert(length2)
              if( length2 < 1){
                that.len2 = true
              }else{
                that.len2 = false
              }
              // that.total = response.data.total;
              // alert(Math.ceil(that.total/10))
            }
          }else{
            let instance = Toast({
              message: response.data.message,
              position: 'bottom',
              duration: 5000
            });
            setTimeout(() => {
              instance.close();
            }, 2000);
          }
        })
        .catch(error => {
          console.log(error);
        });
      },
      // 进入公司预约详情
      toCompanyDetails(id,list){
        console.log(list)
        localStorage.setItem('dataVistorInfo',JSON.stringify(list))
        this.$router.push({
          path: '/details',
          query: {
            visType: 1,
            VisRecordInnerId: id
          }
        })
      },
      // 进入个人预约详情
      toDetails(id,list){
        console.log(list)
        localStorage.setItem('dataVistorInfo1',JSON.stringify(list))
        this.$router.push({
          path: '/details',
          query: {
            visType: 2,
            VisRecordInnerId: id
          }
        })
      },
      haveVisType1(){
        var that = this
        that.visType = 1
        that.page = 1
        that.getRecord()
      },
      haveVisType2(){
        var that = this
        that.visType = 2
        that.page = 1
        that.getRecord()
      },
      // 下拉刷新
      loadTop() {
        this.loadFrist();
      },
      // 上拉加载
      loadBottom() {
        this.loadMore();
      },
      // 下拉刷新
      loadFrist() {
        var that = this
        that.$axios.post("getCompanyVistor",{
          page: 1,
          size: 10,
          OrderType: that.visType,
          VisitorStateHis: '1,2,3,4,5,6,7,8',
          requestType: 1
        })
        .then(response => {
          if( that.visType == 1 ){
            if( response.data.status == 200 ){
              that.courrentPage = 0;
              that.allLoaded = false; // 可以进行上拉
              that.dataCompany = response.data.result;
              that.$refs.loadmore.onTopLoaded();
            }else{
              let instance = Toast({
                message: response.data.message,
                position: 'bottom',
                duration: 5000
              });
              setTimeout(() => {
                instance.close();
              }, 2000);
            }
          }else{
            if( response.data.status == 200 ){
              that.courrentPage = 0;
              that.allLoaded = false; // 可以进行上拉
              that.datas = response.data.result;
              that.$refs.loadmore1.onTopLoaded();
            }else{
              let instance = Toast({
                message: response.data.message,
                position: 'bottom',
                duration: 5000
              });
              setTimeout(() => {
                instance.close();
              }, 2000);
            }
          }
        })
        .catch(error => {
          console.log(error);
        });
      },
      // 上拉加载
      loadMore() {
        var that = this
        that.page++
        that.$axios.post("getCompanyVistor",{
          page: that.page,
          size: 10,
          OrderType: that.visType,
          VisitorStateHis: '1,2,3,4,5,6,7,8',
          requestType: 1
        })
        .then(response => {
          if( that.visType == 1 ){
            if( response.data.status == 200 ){
              // concat数组的追加
              that.dataCompany = that.dataCompany.concat(response.data.result);
              if (that.courrentPage > 2) {
                that.allLoaded = true; // 若数据已全部获取完毕
              }
              that.courrentPage++;
              that.$refs.loadmore.onBottomLoaded();
            }else{
              let instance = Toast({
                message: response.data.message,
                position: 'bottom',
                duration: 5000
              });
              setTimeout(() => {
                instance.close();
              }, 2000);
            }
          }else{
            if( response.data.status == 200 ){
              // concat数组的追加
              that.datas = that.datas.concat(response.data.result);
              if (that.courrentPage > 2) {
                that.allLoaded = true; // 若数据已全部获取完毕
              }
              that.courrentPage++;
              that.$refs.loadmore1.onBottomLoaded();
            }else{
              let instance = Toast({
                message: response.data.message,
                position: 'bottom',
                duration: 5000
              });
              setTimeout(() => {
                instance.close();
              }, 2000);
            }
          }
        })
        .catch(error => {
          console.log(error);
        });
      },
      handleBottomChange(){
        var that = this
        if( that.allLoaded == true ){
          that.more = true
          setTimeout(function(){
            that.more = false
          },2000)
        }
      }
    }
  }
</script>
<style scoped>
  .content {
    overflow: hidden;
  }
  .tabBorder{
    width: 60%;
    height: 54px;
    line-height: 54px;
    margin: 0 auto;
    font-size: 16px;
  }
  .money{
    display: inline-block;
    color: #D71313;
    margin-right: 8px;
    vertical-align: top;
    padding-top: 12px;
  }
</style>
<style>
  /* 预约列表 */
  .mint-navbar .mint-tab-item{
    height: 54px;
    line-height: 54px;
    padding: 0;
  }
  .mint-navbar .mint-tab-item.is-selected {
    border-bottom: none;
    color: #59709A;
    margin-bottom: 0;
    box-sizing: border-box;
  }
  .mint-navbar .mint-tab-item.is-selected .tabBorder {
    border-bottom: 2px solid #59709A;
    color: #59709A;
    box-sizing: border-box;
  }
  .flexBetween{
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
  }
  .main-body { overflow: scroll;/* 加上这个才会有当数据充满整个屏幕，可以进行上拉加载更多的操作 */ }
  .rongUl{ padding: 0px 15px; }
  .plateList{ padding: 10px 0px 20px;min-height: 300px; }
  .plateLi{
    width: 100%;
    height: 62.5px;
    padding: 7.5px 18px;
    margin-top: 10px;
    background:rgba(255,255,255,1);
    border:1px solid rgba(238, 238, 238, 1);
    box-shadow:0px 0px 10px 0px rgba(0,2,11,0.2);
    /* border-radius:0px 20px 20px 20px; */
    border-radius: 10px;
    box-sizing: border-box;
  }
  .listDiv{
    vertical-align: middle;
  }
  .plateVisName{
    display: inline-block;
    width: 66px;
    color: #18191A;
    font-size: 16px;
    font-weight: 400;
    vertical-align: middle;
  }
  .plateNumber{
    display: inline-block;
    width: 116px;
    color: #18191A;
    font-size: 18px;
    font-weight: 400;
    vertical-align: middle;
  }
  .plateTips{
    color: #fff;
    font-size: 10px;
    font-weight: 400;
    padding: 3px 8px 2px;
    /* margin-left: 10px; */
    vertical-align: middle;
    border-radius: 10px;
    background: #FA9819;
  }
  .plateTipsOver{
    background: #29ACEF;
  }
  .plateTipsOverRed{
    background: #E31C1C;
  }
  .plateTipsOverHui{
    background: #545252;
  }
  .plateTipsOverHui2{
    background: #BCBCBC;
  }
  .plateTime{
    color: #18191A;
    font-size: 12px;
    font-weight: 400;
    margin-top: 4px;
  }
  .jian{
    display: inline-block;
    width: 7px;
    height: 44.5px;
    color:#931d36;;
    background: url("../assets/image/jian.png") center center no-repeat;
    background-size: 7px 13px;
  }
  .orderInfo{
    position: fixed;
    left: -10%;
    bottom: 0;
    z-index: 2;
    width: 120%;
    height: 146px;
    background:#931d36;;
    border-top-left-radius: 562px 350px;
    border-top-right-radius: 562px 350px;
    padding: 0 10%;
    box-sizing: border-box;
  }
  .xingz{
    position: absolute;
    top: -1px;
    left: 0;
    z-index: 3;
    width: 100%;
    height: 113px;
  }
  .xingz button{
    position: absolute;
    top: 15px;
    left: 50%;
    z-index: 4;
    color:#931d36;;
    font-size: 14px;
    font-weight: 500;
    font-family: "SourceHanSansCN-Medium";
    width: 75px;
    height: 75px;
    margin-left: -37.5px;
    background: #fff;
    border: none;
    border-radius: 34px;
  }
  .todayCount,.onlyCount{
    width: 25%;
    text-align: center;
    padding: 36px 15px 16px;
    color: #fff;
    font-size: 22px;
    font-weight: 500;
  }
  .todayCount span,.onlyCount span{
    font-size: 12px;
    font-weight: 400;
  }
  .infoHelp{
    width: 100%;
    text-align: center;
    padding: 10px 15px 0;
    box-sizing: border-box;
    color: #fff;
    font-size: 12px;
    font-weight: 400;
    vertical-align: top;
  }
  .question{
    width: 16.5px;
    height: 16.5px;
    background: url("../assets/image/questionSelf.png") center 0 no-repeat;
    background-size: 100% 100%;
  }
  .historyText{
    position: relative;
    left: 9px;
    text-decoration: underline;
  }
  .userInfo{
    position: relative;
    top: -10px;
    width: 32px;
    height: 31.5px;
    background: url("../assets/image/userPhoto.png") center 0 no-repeat;
    background-size: 100% 100%;
  }
  .xingz button{
    color:#931d36;;
    font-size: 12px;
    background: #fff;
  }
  .xingz button img{
    display: block;
    margin: 0 auto 10px;
    width: 29.5px;
    height: 24.5px;
  }
  .noMoreData{
    width: 100%;
    text-align: center;
  }
</style>



